.section__user {
  margin-left: 114px;
  margin-top: 50px;
  width: 474px;
  font-family: Lato;
  font-style: normal;
  color: #494f66;
  .welcome__text {
    font-weight: 300;
    font-size: 36px;
    line-height: 48px;
    letter-spacing: -0.8px;
    span {
      color: #ef3340;
      font-weight: 600;
    }
  }
  .subtitle {
    font-family: Roboto;
    font-weight: 300;
    font-size: 17px;
    line-height: 24px;
    span {
      color: #676f8f;
      font-weight: 1000;
    }
  }

  .input__dni {
    display: flex;
  }
  .input {
    border-radius: 5px;
    padding: 10px 19px 10px;
    width: 100%;
    height: 60px;
    outline: none;
    border: 1px solid #d4d5e8;
    box-sizing: border-box;
    color: #494f66;
    margin: 0;
    font-family: Lato;
  }
  .btn__next {
    display: flex;
    margin-top: 85px;
    justify-content: space-evenly;
    p {
      margin-top: 40px;
      color: #939dff;
    }
  }
}

@media screen and (min-width: 540px) {
  .section__user {
    margin-left: 114px;
    margin-top: 56px;
  }
}
