$red: #ef3340;

.aside__rigth {
  width: 335px;
  margin: 14%;
  margin-left: 25%;

  .intro__title {
    font-family: Lato;
    font-style: normal;
    font-weight: 300;
    font-size: 28px;
    line-height: 36px;
    .intro__title--span {
      color: $red;
      font-weight: 600;
    }
  }

  .input__dni {
    display: flex;
    margin-bottom: 16px;
    
    .MuiNativeSelect-icon {
      color: $red;
    }
  }
  .WithStyles\(ForwardRef\(InputBase\)\)-input-19 {
    padding: 21px 20px 18px 12px;
    width: 65px;
  }
  .input {
    border-radius: 5px;
    padding: 10px 19px 10px;
    width: 100%;
    height: 60px;
    outline: none;
    border: 1px solid #d4d5e8;
    box-sizing: border-box;
    color: #494f66;
    margin: 0;
    font-family: Lato;
  }
  .intro__policies {
    color: #A9B1D1;
    font-family: Roboto;
    font-style: normal;
    font-weight: 300;
    font-size: 13px;
    line-height: 20px;
    letter-spacing: 0.2px;
    margin-top: 20px;
  }
}

@media screen and (min-width: 540px) {

  .aside__rigth {
    width: 335px;
    margin: 18%;
    margin-left: 31%;
  
  }

}
