

.red-image {
  background: #ef3340;
  height: 428px;
  width: 100%;
  border-bottom-right-radius: 7%;
  .line {
    width: 99.6%;
    height: 0px;
    border: 1px solid #f55d68;
  }
  .header {
    height: 64px;
    display: flex;
    align-items: center;
    margin-left: 20%;
  }
  .line {
    width: 99.6%;
    height: 0px;
    border: 1px solid #f55d68;
  }
  .intro__aside--text {
    width: 379px;
    margin: 16% 20%;
    color: #ffffff;
  }
  .img__girl {
    width: 50%;
    position: absolute;
    right: -65%;
    top: 14%;
  }
}

@media screen and (min-width: 540px) {
  .red-image {
    background: #ef3340;
    height: 100%;
    width: 100%;
    border-bottom-right-radius: 3%;
    /*   clip-path : polygon(0 0, 99% 0, 96% 100%, 0% 100%); */

    .intro__aside--text {
      width: 379px;
      margin: 16% 20%;
      color: #ffffff;
      .intro__text {
        font-family: Lato;
        font-style: normal;
        font-weight: 300;
        font-size: 37px;
        line-height: 60px;
        letter-spacing: -0.8px;
        span {
          font-weight: 600;
        }
       
      }
      .intro__p {
        font-family: Roboto;
        font-style: normal;
        font-weight: 100;
        font-size: 18px;
        line-height: 32px;
        span{
          font-weight: 600;
        }
      }
      .sumary__medicine {
        display: flex;
        margin-bottom: 10px;
        p {
          margin: 0;
        }
      }
    }

    .img__girl {
      width: 15%;
      position: absolute;
      right: 41%;
      top: 42%;
    }
  }
}
